
@mixin pad {
	@media screen and (max-width: $pad) {
		@content; } }
@mixin phone {
	@media screen and (max-width: $phone) {
		@content; } }
@mixin pc {
	@media screen and (min-width: $phone+1) {
		@content; } }

@mixin bg($pic) {
  width: image-width($pic);
  height: image-height($pic);
  background-image: image-url($pic); }



@mixin bgi($pic) {
  background-image: image-url($pic); }



@mixin full {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }



@mixin ratio($width, $height) {
	position: relative;
	&:before {
		display: block;
		content: "";
		width: 100%;
		padding-top: $height / $width * 100%; }
	> * {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; } }



@mixin dTime($d:0.3s) {
	transition: all $d {
 } }  // display: flex

@mixin coverBg($pic: null) {
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  @if ($pic) {
    @include bgi($pic); } }



@mixin containBg($pic: null) {
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  @if ($pic) {
    @include bgi($pic); } }

@function strip-unit($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1); }
  @return $number; }

@mixin ratioBg($pic) {
  max-width: image-width($pic);
  width: 100%;
  @include coverBg($pic);
  &:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: strip-unit(image-height($pic)) / strip-unit(image-width($pic)) * 100%; } }
