// index.sass




#index {
  margin-top: 60px;
  .swiper-slide {
    height: calc(100vh - 60px);
    max-height: 700px;
    @include coverBg;
    @include phone {
      max-height: 500px; } }

  .container {
    max-width: 1440px;
    padding: 0 50px;
    padding-top: 50px;
    @include phone {
      padding-top: 24px; }
    .y-btn {
      margin-top: 18px; }

    .txt {
      font-size: 50px;
      font-weight: bold;
      line-height: 150%;
      letter-spacing: 0.15em;
      color: white;
      text-shadow: 2px 2px 5px rgba(black,1), 2px 2px 8px rgba(black,.5);
      strong {
        color: $c-y; }
      .t2 {
        margin-left: 70px; }
      @include phone {
        font-size: 40px;
        &.sm-t {
          font-size: 35px; } } } } }


#about {
  background-color: #c2e9ed;
  position: relative;
  padding-bottom: 440px;
  overflow: hidden;
  p {
    max-width: 630px; }
  strong {
    font-size: 30px;
    font-weight: 500; }
  .pic {
    @include ratioBg('demo-1.png');
    position: absolute;
    bottom: -120px;
    left: 50%;
    transform: translate(-50%, 0);
    @include phone {
      @include ratioBg('demo-1-m.png');
      bottom: 0;
      left: 0;
      transform: translate(0, 0); } } }


#resources {
  background-color: #f8edd9;

  .chats {
    width: 100%;
    max-width: 789px;
    margin: 0 auto;
    position: relative;
    >.chat:not(:first-child) {
      position: absolute;
      top: 0;
      left: 0;
      @include dTime(.3s); }

    .c-1 {
      @include ratioBg('chat-01.png'); }

    .c-2 {
      @include ratioBg('chat-02.png');
      &.active {
        transform: scale(1.02); } }
    .c-3 {
      @include ratioBg('chat-03.png');
      &.active {
        transform: scale(1.02); } }
    .c-4 {
      @include ratioBg('chat-04.png');
      &.active {
        transform: scale(1.02); } }


    .btns {
      @include full;
      .btn {
        position: absolute;

        &.b-1 {
          width: 66%;
          top: 0%;
          left: 0%;
          height: 37%;
          cursor: pointer; }

        &.b-2 {
          width: 33%;
          top: 20%;
          left: 66%;
          height: 60%;
          cursor: pointer; }
        &.b-3 {
          width: 60%;
          top: 50%;
          left: 0%;
          height: 50%;
          cursor: pointer; }
        &.b-4 {
          width: 30%;
          top: 35%;
          left: 35%;
          height: 30%; } } } } }


#process {
  background-color: #edecec;
  img {
    display: block;
    margin: 0 auto; } }

#benefits {
  background-color: $c-y;
  overflow: hidden;
  .picTxt {
    display: flex;
    &.rv {
      @include pc {
        flex-direction: row-reverse; } }

    >* {
      width: 50%;
      padding: 130px 0 100px; }
    @include phone {
      flex-direction: column;
      .pic {
        @include ratio(1,.8);
        padding: 0; }
      > * {
        width: 100%;
        padding: 100px 0; } }

    .pic {
      @include coverBg; }

    .txtBox {
      img {
        display: block;
        margin: 0 auto;
        margin-top: 60px; }

      .p-title {
        .en {
          color: rgba(white,.2);
          // font-size: 64px
          top: -40px; }
        h1 {
          color: white;
          text-shadow: 3px 3px 5px rgba(black,0.15); } } } } }


#works {
  background-color: #edecec;
  .container {
    max-width: 1200px; }
  .swiper-container {
    &.swiper-container-1 {
      width: 100%; } }

  .swiper-container-outer {
    margin-top: 70px;
    .swiper-slide-outer {
      display: flex;
      @include phone {
        flex-direction: column; } } }
  .swiper-container-inner {
    .swiper-slide {
      @include coverBg; }
 }    // height: 550px
  .txtBox {
    background-color: white;
    width: 550px;
    overflow-y: scroll;
    height: 450px;
    @include phone {
      height: auto; }
    .txt-container {
      width: 100%;
      padding: 30px 20px;
      line-height: 120%; }

    .p-content {
      background-color: $c-y;
      padding: 10px 15px;
      color: white; }
    h2 {
      margin-bottom: 30px;
      font-size: 24px; }
    p {
      line-height: 150%;
      margin-bottom: 30px;
      font-size: 18px;
      letter-spacing: auto;
      @include phone {
        font-size: 24px; } } }
  .pic {
    @include phone {
      height: 400px; } }
  .videoBtn {
    padding: 8px 32px;
    background-color: $c-y;
    display: inline-block;
    border-radius: 50px;
    font-size: 18px;
    cursor: pointer;
    margin-bottom: 25px;
    @include dTime;
    .i-play {
      @include dTime(.3s); }
    &:hover {
      background-color: lighten($c-y,8%);
      .i-play {
        transform: translate(5px, 0); } } }

  .sliderBtns {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;
    .sliderBtn {
      width: 50px;
      height: 50px;
      background-color: white;
      border-radius: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 10px;
      cursor: pointer;
      @include dTime;
      @include phone {
        margin: 0 5px; }
      &:hover {
        background-color: rgba($c-y,1); }
      &.active {
        background-color: rgba($c-y,1);
        width: 100px; } } } }



.banner {
  width: 100%;
  height: 466px;
  @include coverBg('banner.jpg');
  background-position: 100% 50%;
  @include phone {
    height: 300px; } }


#qa {
  background-color: #f2f2f2;

  .accordion {
    margin-top: 70px; }

  .qaBox {
    padding: 28px 45px;
    background-color: #fff;
    border: 1px solid rgba($c-y,.8);
    border-radius: 8px;
    margin-top: 15px;
    cursor: pointer;
    &.active {
      .qBox {
        &::after {
          transform: rotate(0deg); } }
      .aBox {
        margin-top: 20px;
        opacity: 1; } }

    .qBox {
      font-size: 24px;
      position: relative;
      padding-left: 45px;
      line-height: 150%;
      &::before {
        content: 'Q: ';
        position: absolute;
        top: 0;
        left: 0; }
      &:after {
        position: absolute;
        content: '';
        @include bg('arr-u.png');
        top: 8px;
        right: -15px;
        @include dTime;
        transform: rotate(180deg); } }
    .aBox {
      font-size: 20px;
      color: #383838;
      line-height: 170%;
      overflow: hidden;
      opacity: 0;
      // margin-top: 25px
      padding-left: 45px;
      height: 0;
      @include dTime;
      img {
        display: block; }
      a {
        color: blue; } } } }


#fb-btn {
  position: fixed;
  z-index: 5;
  @include bg('fbBtn.png');
  cursor: pointer;
  display: block;
  right: 16px;
  bottom: 16px;
  @include dTime(.3s);
  &:hover {
    opacity: .9;
    transform: scale(1.05); } }
